// css header
.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: var(--z-fixed);
    --height-top:
    &__top {
        width: 100%;
        height: 7.2rem;
        background-color: $header-top;
        .container {
            position: relative;
            width: 100%;
            height: 100%;
        }
        img {
            @include midimg(100%);
        }
        &-logo {
            @include mid;
            width: 4.3rem;
            padding-top: 4.3rem;
            overflow: hidden;
        }
        &-facebook {
            position: absolute;
            top: 50%;
            right: 0;
            width: 0.9rem;
            padding-top: 1.9rem;
            margin-right: 0.6rem;
            overflow: hidden;
            @include transform(translateY(-50%));
        }
    }
    &__nav {
        width: 100%;
        height: calc(var(--height-header) - 7.2rem);
    }
}
